@import "sass-rem";

.promoSlider {
	margin-bottom: rem(40px);
	display: flex;
}

.promoSliderLeft {
	width: rem(390px);
}

.promoSliderRight {
	width: rem(1030px);
}

.promoSliderLeft {
	position: relative;
}

.infoWrapper {
	position: relative;
	height: rem(430px);

	&:before {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		right: 0;
		height: rem(4px);
		background: radial-gradient(50% 50% at 50% 50%, #F7FF13 0%, rgba(0, 0, 0, 0) 100%);
	}

	&:after {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		right: 0;
		height: rem(361px);
		background: radial-gradient(63.59% 100% at 50% 0%, #494B02 0%, rgba(69, 71, 13, 0.571) 41.15%, rgba(49, 50, 9, 0.34) 72.92%, rgba(79, 82, 10, 0) 100%);
		transform: matrix(-1, 0, 0, 1, 0, 0);
	}
}

.infoContent {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	height: rem(371px);
	padding: rem(54px 54px 0 15px);
	z-index: 2;
	display: flex;
	flex-direction: column;
}

.date {
	padding-left: rem(28px);
	margin-bottom: rem(20px);
	font-weight: 400;
	font-size: rem(13px);
	line-height: rem(15px);
	color: #9C9C9C;
	position: relative;

	&:before {
		content: "";
		position: absolute;
		top: 35%;
		left: 0;
		width: rem(18px);
		height: rem(18px);
		background: url("../../assets/images/svg/promotions-time.svg") no-repeat 50% 50% /contain;
		transform: translateY(-50%);
	}
}

.title {
	font-weight: 500;
	font-size: rem(26px);
	line-height: rem(30px);        
	color: #fff;
	margin-bottom: rem(15px);
}

.text {
	font-weight: 400;
	font-size: rem(16px);
	line-height: rem(18px);        
	color: #8F8F8F;
	margin-bottom: auto;
}

.btnWrapper {
	display: flex;
	gap: rem(10px);
}

.btn {
	display: inline-block;
	background: rgba(255, 255, 255, 0.15);
	border-radius: 3px;
	padding: rem(16px 32px);
	font-weight: 700;
	font-size: rem(16px);
	line-height: rem(19px);
	text-transform: uppercase;
	color: #fff;

	&.btnAccent {
		color: #000;
		background: #FAFE13;
	}
}

.imgWrapper {
	height: rem(430px);
}

.img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}
